<template>
<nav>
 <div class="nav-wrapper green">
  <div class="container">
   <router-link to="/" class="brand-logo">
    Employee Manager
   </router-link>
   <ul class="right">
    <li v-if="isLoggedIn">
     <span class="black-text email">{{currentUser}}</span>
    </li>
    <li v-if="isLoggedIn">
     <router-link to="/">
      Dashboard
     </router-link>
    </li>
    <li v-if="!isLoggedIn">
     <router-link to="/login">
      Login
     </router-link>
    </li>
    <li v-if="!isLoggedIn">
     <router-link to="/register">
      register
     </router-link>
    </li>
    <li v-if="isLoggedIn"><button class="btn black" @click.prevent="logout">Logout</button></li>
   </ul>
  </div>
 </div>
</nav>
</template>
<script>
import init from '../firebase_services/firebaseinit'
import auth from '../firebase_services/auth'

export default {
 name: 'navbar',
 data (){
  return {
   isLoggedIn:false,
   currentUser:null
  }
 },
 created(){
  init
  if(auth.currentUser){
   this.isLoggedIn=true
   this.currentUser=auth.currentUser.email
  }
 },
 // mounted(){
 // },
 
 methods:{
  logout(){
   auth.signOut().then(()=>{
    this.$router.push({name:'login'},()=>{})
   })
  }
 }
}
</script>
<style scoped>
.email{
 padding-right: 10px;
}
</style>
