import Vue from "vue"
import VueRouter from "vue-router"
import firebase from "firebase/app"

// console.log('user', firebase.auth().currentUser)
Vue.use(VueRouter)

const routes = [
  {
    path: "/",
    redirect: {
      name: "dashboard"
    }
  },
  {
    path: "/dashboard",
    name: "dashboard",
    component: () => import(/* webpackChunkName: "about" */ "../views/Dashboard.vue"),
    meta:{requiresAuth:true}
  },
  {
    path: "/login",
    name: "login",
    component: () => import(/* webpackChunkName: "login" */  "../views/Login.vue"),
    meta:{requiresGuets:true}
  },
  
  {
    path: "/register",
    name: "register",
    component: () => import( /* webpackChunkName: "register" */ "../views/Register.vue"),
    meta:{requiresGuets:true}
  },
  {
    path: "*",
    name: "404",
    component: () => import(/* webpackChunkName: "404" */  "../views/Home.vue"),
    meta:{requiresAuth:true}
  },
  {
    path: "/new",
    name: "new-employee",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "new-employee" */ "../views/NewEmployee.vue"),
    meta:{requiresAuth:true}
  },
  {
    path: "/edit/:employee_id",
    name: "edit-employee",
    component: () => import(/* webpackChunkName: "edit-employee" */  "../views/EditEmployee.vue"),
    meta:{requiresAuth:true}
  },
  {
    path: "/:employee_id",
    name: "view-employee",
    component: () => import(/* webpackChunkName: "view-employee" */  "../views/ViewEmployee.vue"),
    meta:{requiresAuth:true}
  },
]

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next)=>{
  // check for requiresAuth
  if(to.matched.some(record=>record.meta.requiresAuth)){
    //check if not logged in
    if(!firebase.auth().currentUser){
      next({path: '/login'})
    }else{
      next()
    }
  }else if(to.matched.some(record=>record.meta.requiresGuets)){
    if(firebase.auth().currentUser){
      next({path: '/'})
    }else{
      next()
    }
  }else{
    next()
  }
})
export default router
